
.experience-block {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 0 2vh 0;
    padding: 0 0 3vh 0;
}

.experience-block  .title-and-date-container, .subject {
    display: flex;
    width: 100%;
    justify-content: space-between;
    flex-wrap:wrap;
}

.experience-block .internship-title {
    font-family: "Coolvetica";
    letter-spacing: .15rem;
    margin-bottom: 0em;
    margin-top: 0.3em;
}

    .experience-block .subject {
        font-family: "Coolvetica italic";
        letter-spacing: .15rem;
        margin-top: 1vh;
    }

.experience-block .subject > p {
    margin-bottom: 0em;
    margin-top: 0.2em;
}
.image-container {
   float: left;
   padding: 0 2vw 5vh 0;
}

.image {
    width:85vw;
    max-width: 800px;
    border-radius: 5%;
}
